.main-nav__wrapper {
  min-width: 100%;
}
.main-nav__toggle {
  position: absolute;
  right: 21px;
  z-index: 2;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle {
  top: 20px;
  width: 50px;
  height: 24px;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 5px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 30%;
}
.main-nav--opened .main-nav__toggle {
  top: -50px;
  width: 25px;
  height: 25px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -4px;
  width: 30px;
  height: 5px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 30%;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item a {
  display: block;
  padding: 16px 0 15px;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  color: #FFFFFF;
  text-decoration: none;
}
.main-nav__item a[href]:hover {
  color: #d22856;
}
.main-nav__item a[href]:active {
  opacity: 30%;
}
@media (max-width: 959px) {
  .main-nav__wrapper {
    margin: 0 auto;
    background-color: #283645;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 69px;
    left: 0;
    z-index: 2;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav__item {
    border-top: 1px solid #000000;
    box-shadow: 0 1px 0 0 #000000, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
  }
  .main-nav__item a {
    text-align: center;
    text-transform: uppercase;
  }
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__wrapper {
    top: 110px;
  }
  .main-nav--opened .main-nav__toggle {
    top: -66px;
  }
  .main-nav--closed .main-nav__toggle {
    top: 43px;
  }
}
@media (min-width: 960px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    min-width: 623px;
  }
  .main-nav__item--active {
    border-bottom: 1px solid #ffffff;
  }
